export default {
    mergeArrToObjArr(arr0, arr1) {
        let arr = [];
        for (let i = 0; i < arr0.length; i++) {
            arr.push({
                id: arr0[i],
                address: arr1[i],
                usdtAmount: 0,
                chipsAmount: 0
            })
        }
        return arr;
    },
    fromWeiByEther(num) {
        if (num) {
            return parseFloat(web3.utils.fromWei(num, 'ether'))
        }
        return 0;
    },
    toWeiByEther(num) {
        num = parseFloat(num)
        if (num) {
            num *= 1000000;
            return web3.utils.toWei(num.toFixed(0).toString(), 'micro')
        }
        return 0;
    }
}