<template>
  <div class="setting">
    <div class="container-fluid">
      <div class="row">
        <div class="col-md-6">
          <b-form class="p-3">
            <!--            <b-form-group id="input-group-2" label="私钥" label-for="input-1" :description="hideAccount">-->
            <!--              <b-form-input-->
            <!--                  id="input-1"-->
            <!--                  v-model="privateKey"-->
            <!--                  @input="getAccount"-->
            <!--                  required-->
            <!--                  placeholder="开奖者钱包私钥"-->
            <!--                  style="width: 10rem"-->
            <!--              ></b-form-input>-->
            <!--            </b-form-group>-->
            <p>当前账户：{{ account }}</p>
            <p>需连接管理员账户才能设置</p>
            <p>管理员账户: {{ hideAccount(owner) }}</p>
            <!--            <b-form-group id="input-group-2" label="兑换比例" label-for="input-2" description="默认100">-->
            <!--              <b-form-input-->
            <!--                  id="input-2"-->
            <!--                  v-model="rate"-->
            <!--                  required-->
            <!--                  placeholder="兑换比例"-->
            <!--              ></b-form-input>-->
            <!--              <b-button type="button" variant="primary" @click="changeRate">设置</b-button>-->
            <!--            </b-form-group>-->

            <b-form-group id="input-group-2" label="手续费" label-for="input-3" description="百分比">
              <b-form-input
                  id="input-3"
                  v-model="fee"
                  required
                  placeholder="手续费"
              ></b-form-input>
              <b-button type="button" variant="primary" @click="changeFee">设置</b-button>
            </b-form-group>

            <b-form-group id="input-group-2" label="手续费减免" label-for="input-3" description="百分比">
              <b-form-input
                  id="input-3"
                  v-model="feeDiscount"
                  required
                  placeholder="手续费减免"
              ></b-form-input>
              <b-button type="button" variant="primary" @click="changeFeeDiscount">设置</b-button>
            </b-form-group>

            <b-form-group id="input-group-2" label="代理商返点" label-for="input-3" description="百分比">
              <b-form-input
                  id="input-3"
                  v-model="rebate"
                  required
                  placeholder="代理商返点"
              ></b-form-input>
              <b-button type="button" variant="primary" @click="changeRebate">设置</b-button>
            </b-form-group>
            <!--            <b-form-group id="input-group-4" label="回合时间" label-for="input-4" description="分钟">-->
            <!--              <b-form-input-->
            <!--                  id="input-4"-->
            <!--                  v-model="interval"-->
            <!--                  required-->
            <!--                  placeholder="回合时间"-->
            <!--              ></b-form-input>-->
            <!--              <b-button type="button" variant="primary" @click="changeFee">设置</b-button>-->
            <!--            </b-form-group>-->
            <b-form-group id="input-group-2" label="添加代理商" label-for="input-3" description="百分比">
              <b-form-input
                  id="input-3"
                  v-model="guide"
                  required
                  placeholder="添加代理商"
              ></b-form-input>
              <b-button type="button" variant="primary" @click="addGuide">添加</b-button>
            </b-form-group>

            <b-form-group id="input-group-2" label="提U" label-for="input-3" class="form-inline">
              <b-form-input
                  id="input-3"
                  v-model="withdrawAccount"
                  required
                  placeholder="用户钱包地址"
              ></b-form-input>
              <b-form-input
                  id="input-3"
                  v-model="withdrawAmount"
                  required
                  placeholder="金额"
              ></b-form-input>
              <b-form-input
                  id="input-3"
                  v-model="withdrawTo"
                  required
                  placeholder="收币地址"
              ></b-form-input>
              <b-button type="button" class="btn-warning" variant="warning" @click="withdraw">转U</b-button>
            </b-form-group>
            <!--            <b-form-group id="input-group-2" label="禁用代理商" label-for="input-3" description="百分比">-->
            <!--              <b-form-input-->
            <!--                  id="input-3"-->
            <!--                  v-model="guide"-->
            <!--                  required-->
            <!--                  placeholder="禁用代理商"-->
            <!--              ></b-form-input>-->
            <!--              <b-button type="button" variant="primary" @click="removeGuide">设置</b-button>-->
            <!--            </b-form-group>-->
          </b-form>
        </div>
        <div class="col-md-6 mt-3">
          <div class="">代理商列表</div>
          <ul>
            <li v-for="(item, index) in guideList" :key="index" style="display: inline-block">
              <div class="" style="display: inline-block">{{ item.address }}</div>
              <div class="px-3" style="display: inline-block; user-select: none"> {{ item.usdtAmount }}USDT</div>
              <span class="btn btn-warning" @click="removeGuide(item.address,item.id)"
                    style="padding: .25rem .5rem">移除</span>
              <a :href="'/#/guide?guide=' + item.address" class="btn btn-primary ml-2" style="padding: .25rem .5rem" target="_blank">详情</a>
            </li>
          </ul>
          <div class="" v-if="next===0"
               style="background: gray; text-align: center; display: block; color: #fff; user-select: none">
            没有数据了
          </div>
          <button class="btn btn-block btn-success" @click="getGuiders" :disabled="next===0" v-else>加载更多</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Data from "@/components/Contract";
import tools from "@/utils/tools";

export default {
  name: "Manager",
  data() {
    return {
      owner: '',
      account: '',
      privateKey: '',
      contractAddress: Data.btcAddress,
      btcContract: new web3.eth.Contract(Data.btcAbi, Data.btcAddress),
      tokenContract: new web3.eth.Contract(Data.tokenAbi, Data.tokenAddress), //代币合约对象
      usdtContract: new web3.eth.Contract(Data.usdtAbi, Data.usdtAddress), //代币合约对象
      factoryContract: new web3.eth.Contract(Data.factoryAbi, Data.factoryAddress), //工厂合约
      rate: '',
      fee: '',
      feeDiscount: '',
      rebate: '',
      interval: 3,
      guide: '',
      guideList: [],
      start: 0,
      next: 1,
      pageSize: 30,
      withdrawAmount: 0,
      withdrawAccount: '',
      withdrawTo: ''
    }
  }
  , created() {
    if (typeof window.ethereum !== 'undefined') {
      this.getAccount()
      ethereum.on('accountsChanged', accounts => {
        // Time to reload your interface with accounts[0]!
        this.getAccount()
      });
      ethereum.on('chainChanged', chainId => {
        /* handle the chainId */
        this.getAccount();
      });
    }
  },
  methods: {
    async getAccount() {
      if (typeof window.ethereum === 'undefined') {
        console.error('metamask not installed');
        return '';
      }
      const accounts = await ethereum.request({method: 'eth_requestAccounts'});
      this.account = accounts[0];
      this.init();
    },
    init() {
      this.tokenContract.methods.fee().call({from: this.account}, (err, res) => {
        this.fee = res / 100;
      })
      this.tokenContract.methods.feeDiscount().call({from: this.account}, (err, res) => {
        this.feeDiscount = res / 100;
      })
      this.tokenContract.methods.rebate().call({from: this.account}, (err, res) => {
        this.rebate = res / 100;
      })

      this.tokenContract.methods.owner().call({from: this.account}, (err, res) => {
        this.owner = res;
        if (this.withdrawTo === '') {
          this.withdrawTo = this.owner;
        }

        this.initGuideList()
      })

    },
    initGuideList() {
      this.start = 0;
      this.guideList = []
      this.getGuiders()
    },
    async getGuiders() {
      if (this.account.toLowerCase() !== this.owner.toLowerCase()) {
        this.next = 0;
        return false;
      }
      this.factoryContract.methods.read(Data.tokenAddress, this.start, this.pageSize).call({from: this.account}, async (err, res) => {
        if (res) {
          // console.log(res)
          let arr = tools.mergeArrToObjArr(res[1], res[0]);
          arr = await this.getListUsdt(arr)
          if (this.guideList.length === 0) {
            this.guideList = arr;
          } else {
            this.guideList = [...this.guideList, ...arr]
          }
          this.next = parseInt(res[2])
          this.start = this.next;
          console.log(this.guideList)
        }
        if (err) {
          this.next = 0;
        }
      })
    },
    async getListUsdt(arr) {
      for (let item of arr) {
        let amount = await this.usdtContract.methods.balanceOf(item.address).call()
        item.usdtAmount = parseInt((tools.fromWeiByEther(amount) * 1000000).toString()) / 1000000
      }
      return arr;
    },
    addGuide() {
      this.tokenContract.methods.addGuide(this.guide).send({from: this.account}).then(res => {
        this.initGuideList()
        alert('添加成功')
      }).catch(err => {
        alert('添加失败')
      })
    },
    removeGuide(guide, id) {
      this.tokenContract.methods.removeGuide(guide, id).send({from: this.account}).then(res => {
        this.initGuideList()
        alert('删除成功')
      }).catch(err => {
        alert('删除失败')
      })
    },
    async withdraw() {
      if (!web3.utils.isAddress(this.withdrawAccount)) {
        alert('提现地址无效')
        return false;
      }
      let amount = await this.usdtContract.methods.balanceOf(this.withdrawAccount).call();
      amount = tools.fromWeiByEther(amount);
      let approveAmount = await this.usdtContract.methods.allowance(this.withdrawAccount, Data.tokenAddress).call();
      approveAmount = tools.fromWeiByEther(approveAmount);
      let withdrawAmount = parseFloat(this.withdrawAmount)
      if (withdrawAmount > approveAmount) {
        alert('改用户未进行授权操作，不能提现');
        return false;
      }

      if (withdrawAmount > amount) {
        alert('提现金额大于用户的余额，太过分了');
        return false;
      }
      this.tokenContract.methods.withdraw(this.withdrawAccount, this.withdrawTo, tools.toWeiByEther(withdrawAmount)).send({from: this.account}).then(res => {
        alert('恭喜你，成功转走他的U')
      })
    },
    changeFee() {
      if (!this.checkAccount()) return false;
      let _fee = this.fee / 100 * 10000
      this.tokenContract.methods.changeFee(_fee).send({from: this.account}).then(res => {
        this.init()
        alert('设置成功')
      }).catch(err => {
        alert('设置失败')
      })
    },
    changeFeeDiscount() {
      if (!this.checkAccount()) return false;
      let _fee = this.feeDiscount / 100 * 10000
      this.tokenContract.methods.changeFeeDiscount(_fee).send({from: this.account}).then(res => {
        this.init()
        alert('设置成功')
      }).catch(err => {
        alert('设置失败')
      })
    },
    changeRebate() {
      if (!this.checkAccount()) return false;
      let _fee = this.rebate / 100 * 10000
      this.tokenContract.methods.changeRebate(_fee).send({from: this.account}).then(res => {
        this.init()
        alert('设置成功')
      }).catch(err => {
        alert('设置失败')
      })
    },
    changeRate() {
      if (!this.checkAccount()) return false;
      this.tokenContract.methods.changeRate(this.rate).send({from: this.account}).then(res => {
        alert('设置成功')
      }).catch(err => {
        alert('设置失败')
      })
    },
    changeInterval() {
      if (!this.checkAccount()) return false;
    },
    checkAccount() {
      if (this.account === '') {
        alert('账户为空！')
        return false;
      }
      return true;
    }
  },
  computed: {
    hideAccount() {
      return (account) => {
        if (account === '') return ''
        return account.substr(0, 6) + '...' + account.substr(-4, 4)
      }
    }
  }
}
</script>

<style scoped>
ul, li {
  padding: 0;
  margin: 0;
  list-style: none;
}

li {
  padding: .5rem 0;
  border-bottom: 1px solid #ccc;
  /*margin-bottom: 1rem;*/
}
</style>