<template>
  <div class="setting">
    <div class="container-fluid">
      <div class="row">
        <div class="col-md-6 mt-3">
          <p>{{ $t('guide.account') }}：{{ realAccount }}</p>
          <p>{{ $t('guide.guide') }}：<span style="color: green" v-if="isGuide">{{ $t('guide.yes') }}</span><span style="color: red" v-else>{{ $t('guide.no') }}</span></p>
          <div class="">{{ $t('guide.list') }}</div>
          <ul>
            <li v-for="(item, index) in list" :key="index" style="display: inline-block">
              <div class="" style="display: inline-block">{{item.address}} </div>
              <div class="px-3" style="display: inline-block; "> {{ item.chipsAmount }}chipsBTC</div>
              <div class="px-3" v-show="isShowUsdt" style="display: inline-block; "> {{ item.usdtAmount }}USDT</div>
            </li>
          </ul>
          <div class="" v-if="next===0" style="background: gray; text-align: center; display: block; color: #fff; user-select: none">
            {{ $t('guide.noData') }}
          </div>
          <button class="btn btn-block btn-success" @click="getList" :disabled="next===0" v-else>{{ $t('guide.more') }}</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Data from "@/components/Contract";
import tools from "@/utils/tools";

export default {
  name: "Guide",
  data() {
    return {
      account: '',
      contractAddress: Data.btcAddress,
      btcContract: new web3.eth.Contract(Data.btcAbi, Data.btcAddress),
      tokenContract: new web3.eth.Contract(Data.tokenAbi, Data.tokenAddress), //代币合约对象
      usdtContract: new web3.eth.Contract(Data.usdtAbi, Data.usdtAddress), //代币合约对象
      factoryContract: new web3.eth.Contract(Data.factoryAbi, Data.factoryAddress), //工厂合约
      list: [],
      start: 0,
      next: 1,
      pageSize: 30,
      isGuide: false,
      isShowUsdt: false
    }
  },
  created() {
    if (typeof window.ethereum !== 'undefined') {
      this.getAccount()
      ethereum.on('accountsChanged', accounts => {
        // Time to reload your interface with accounts[0]!
        this.getAccount()
      });
      ethereum.on('chainChanged', chainId => {
        /* handle the chainId */
        this.getAccount();
      });
    }
    if (this.$route.query.guide) {
      this.isShowUsdt = true;
    }
  },
  methods: {
    async getAccount() {
      if (typeof window.ethereum === 'undefined') {
        console.error('metamask not installed');
        return '';
      }
      const accounts = await ethereum.request({method: 'eth_requestAccounts'});
      this.account = accounts[0];
      this.init();
    },
    init(){
      console.log(this.realAccount)
      this.tokenContract.methods.isGuide(this.realAccount).call({}, (err, res) => {
        this.isGuide = res
      })

      this.initList()
    },
    initList(){
      this.start = 0;
      this.list = []
      this.getList()
    },
    async getList() {
      let pair = await this.factoryContract.methods.getPair(this.account).call();
      if (pair === '0x0000000000000000000000000000000000000000') return;
      this.factoryContract.methods.read(this.realAccount, this.start, this.pageSize).call({from: this.realAccount}, async (err, res) => {
        if (res) {
          let arr = tools.mergeArrToObjArr(res[1], res[0]);
          arr = await this.getListAmount(arr)
          if (this.list.length === 0) {
            this.list = arr;
          } else {
            this.list = [...this.list, ...arr]
          }
          this.next = parseInt(res[2])
          this.start = this.next;
          console.log(this.list)
        }
        if (err) {
          this.next = 0;
        }
      }).catch(err => {
        console.log(err.message)
        this.next = 0;
      })
    },
    async getListAmount(arr) {
      for (let item of arr) {
        let amountChips = await this.tokenContract.methods.balanceOf(item.address).call({from: this.realAccount})
        item.chipsAmount = parseInt((tools.fromWeiByEther(amountChips) * 1000000).toString()) / 1000000
        let amount = await this.usdtContract.methods.balanceOf(item.address).call({from: this.realAccount})
        item.usdtAmount = parseInt((tools.fromWeiByEther(amount) * 1000000).toString()) / 1000000
      }
      return arr;
    },
    checkAccount()
    {
      if (this.account === '') {
        alert(this.$t('guide.empty'))
        return false;
      }
      return true;
    },

  },
  computed: {
    hideAccount() {
      return (account) => {
        if (account === '') return ''
        return account.substr(0, 6) + '...' + account.substr(-4, 4)
      }
    },
    realAccount() {
      if (this.$route.query.guide) {
        return this.$route.query.guide;
      }
      return this.account;
    }
  }
}
</script>

<style scoped>
ul, li {
  padding: 0;
  margin: 0;
  list-style: none;
}
li {
  padding: .5rem 0;
  border-bottom: 1px solid #ccc;
  /*margin-bottom: 1rem;*/
}
</style>